@import url(https://fonts.googleapis.com/css2?family=Caveat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Caveat&family=Permanent+Marker&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Yusei+Magic&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Yusei+Magic&display=swap);
body {
  background-color: #460031;
  margin: auto;
  color: white;
  font-family: "Yusei Magic", sans-serif;
}

.App {
  text-align: center;
  /* font-size: 2rem;
  font-family: "Caveat", cursive; */
  /* font-family: "Yusei Magic", sans-serif; */
  /* font-size: 8px; */
}

input {
  font-family: "Yusei Magic", sans-serif;
  border: 1px solid white;
  margin: 1rem 0;
  border-radius: 5px;
  height: 2rem;
  text-align: center;
  background-color: transparent;
  color: white;
}

button {
  font-family: "Yusei Magic", sans-serif;
  border: 2px solid black;
  margin: 1rem 0;
  border-radius: 5px;
  height: 2rem;
  color: black;
  background-color: white;
  cursor: pointer;
}


.arts {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-content: space-around;
  grid-column-gap: 2rem;
  -webkit-column-gap: 2rem;
          column-gap: 2rem;
  grid-row-gap: 4rem;
  row-gap: 4rem;
  margin: 2rem 0;
}

.card {
  display: flex;
  flex-direction: column;
  grid-row-gap: 0.7rem;
  row-gap: 0.7rem;
  width: 12rem;
  /* height: 16rem; */
  text-align: center;
  /* border: 2px solid black; */
  /* background-color: white; */
  border-radius: 5px;
  /* box-shadow: 0px 0px 8px 0 rgba(255, 255, 0, 1); */
  text-decoration: none;
  color: inherit;
}

#name {
  font-size: 1rem;
}

.wrapper {
  /* width: 100%;
    height: 100%; */
  height: 50%;
  background-color: rgb(209, 209, 209);
  border: 3px solid black;
  border-radius: 5px;

  transition: 0.2s;
}

.wrapper:hover {
  /* cursor: pointer; */
  border: 3px solid white;
  transition: 0.2s;
}

.wrapper > a > img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  /* border-radius: 5px; */
}

#bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.7rem;
}

#user-info {
  display: flex;
  justify-content: right;
  align-items: center;
  grid-column-gap: 0.4rem;
  -webkit-column-gap: 0.4rem;
          column-gap: 0.4rem;
}

#user-info img {
  height: 1.5rem;
  width: 1.5rem;
  object-fit: cover;
  border-radius: 100%;
  border: 2.5px solid black;
  transition: 0.3s;
}

#user-info img:hover {
  border: 2.5px solid white;
}

#user-info :first-child:hover + a > img {
  background-color: yellow;
  border: 2.5px solid white;
}

#user-info a {
  text-decoration: none;
}

#heart {
  color: rgb(167, 167, 167);
}

#user .photo-wrapper {
  width: 10rem;
  height: 10rem;
  margin: 1rem auto;
}

#user .photo-wrapper > img {
  cursor: pointer;
  object-fit: cover;
  border-radius: 100%;
  border: 3px solid black;
  width: 100%;
  height: 100%;
  margin: auto;
}

#delete {
  cursor: pointer;
  color: white;
  float: right;
  font-size: 16px;
  transition: 0.3s;
}

#delete:hover {
  color: red;
  transition: 0.3s;
}

textarea {
  /* display: block; */
  border: none;
  color: rgb(211, 211, 211);
  font-size: inherit;
  text-align: center;
  font-family: inherit;
  background-color: inherit;
}

#save {
  display: block;
  margin: auto;
  text-align: center;
  cursor: pointer;
  transition: 0.3s;
}

#save:hover {
  color: rgb(0, 173, 0);
  transition: 0.3s;
}

#login form,
#register form {
  font-family: "Yusei Magic", sans-serif;
  width: 40vw;
  margin: 1rem auto;
  padding: 1rem 1rem;
  display: flex;
  flex-direction: column;
  border: 5px solid white;
  border-radius: 12px;
}

#login img,
#register img {
  margin-top: 2rem;
}

.error {
  height: 1rem;
  font-size: 13px;
  color: rgb(240, 0, 0);
}

#not-a-user {
  font-family: "Yusei Magic", sans-serif;
  font-size: 18px;
  margin-bottom: 3rem;
}

#nav {
  display: flex;
  padding-left: 0;
  padding-right: 1rem;
  justify-content: flex-end;
  align-items: center;
  grid-column-gap: 3rem;
  -webkit-column-gap: 3rem;
          column-gap: 3rem;
  margin: auto;
  list-style: none;
  background-color: black;
  /* overflow: hidden; */
  font-size: 0.8rem;
  font-family: "Permanent Marker", cursive;
  height: 2.6rem;
  /* vertical-align: middle; */
}

li:first-child {
  margin-right: auto;
  color: #4caf50;
}

li > a {
  cursor: pointer;
  /* float: right; */
  color: white;
  /* text-align: center;*/
  /* padding: 4px 16px; */
  text-decoration: none;
  transition: 0.3s;
}

li > a:hover {
  color: #a50073;
  transition: 0.3s;
}

li {
  display: flex;
  align-items: center;
  grid-column-gap: 0.4rem;
  -webkit-column-gap: 0.4rem;
          column-gap: 0.4rem;
}
li > a.active {
  background-color: #4caf50;
  color: white;
}

#nav > img {
  float: left;
  height: 100%;
}

#nav-pic {
  height: 1.5rem;
  width: 1.5rem;
  object-fit: cover;
  border-radius: 100%;
  border: 2.5px solid #a50073;
  /* display: inline; */
}

#logo {
  height: 2.5rem;
}

#brush {
  font-size: 18px;
  padding-top: 6px;
}

